<template>
  <div class="my-overtime" v-loading="loading">
    <div class="header">
      <div class="filters">
        <el-select
          v-model="filters.trang_thai"
          placeholder="Trng thi"
          clearable
          @change="handleFilterChange"
        >
          <el-option label="Tt c" value="" />
          <el-option label="Ch duyt" value="Cho duyet" />
          <el-option label=" duyt" value="Da duyet" />
          <el-option label="B t chi" value="Bi tu choi" />
          <el-option label=" hy" value="Da huy" />
        </el-select>
      </div>
      <el-button type="primary" :icon="Plus" @click="openDialog">
        ng k tng ca
      </el-button>
    </div>

    <el-table
      :data="requests"
      border
      :empty-text="loading ? 'ang ti...' : 'Cha c yu cu'"
    >
      <el-table-column prop="ngay" label="Ngy" width="140">
        <template #default="{row}">
          {{ formatDate(row.ngay) }}
        </template>
      </el-table-column>
      <el-table-column label="Khong thi gian" min-width="200">
        <template #default="{row}">
          {{ formatTimeRange(row) }}
        </template>
      </el-table-column>
      <el-table-column prop="so_gio" label="S gi" width="100">
        <template #default="{row}">
          {{ row.so_gio?.toFixed(2) }}h
        </template>
      </el-table-column>
      <el-table-column label="Loi" min-width="160">
        <template #default="{row}">
          {{ typeLabel(row.loai_ngay) }}
        </template>
      </el-table-column>
      <el-table-column label="Trng thi" width="140">
        <template #default="{row}">
          <el-tag :type="statusTag(row.trang_thai)">
            {{ statusLabel(row.trang_thai) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="Hnh ng" width="160" align="center">
        <template #default="{row}">
          <el-button
            v-if="row.trang_thai === 'Cho duyet'"
            type="danger"
            link
            size="small"
            :loading="cancellingId === row._id"
            @click="handleCancel(row._id)"
          >
            Hy yu cu
          </el-button>
          <span v-else>--</span>
        </template>
      </el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="pagination.total"
        :page-size="pagination.limit"
        :current-page="pagination.page"
        @current-change="handlePageChange"
      />
    </div>

    <el-dialog v-model="dialogVisible" title="ng k tng ca" width="520px">
      <el-form :model="form" :rules="rules" ref="formRef" label-width="160px">
        <el-form-item label="Ngy tng ca" prop="ngay">
          <el-date-picker
            v-model="form.ngay"
            type="date"
            placeholder="Chn ngy"
            format="DD/MM/YYYY"
            value-format="YYYY-MM-DD"
            style="width: 100%"
          />
        </el-form-item>
        <el-form-item label="Gi bt u" prop="gio_bat_dau">
          <el-time-picker
            v-model="form.gio_bat_dau"
            placeholder="HH:mm"
            format="HH:mm"
            value-format="HH:mm"
            style="width: 100%"
          />
        </el-form-item>
        <el-form-item label="Gi kt thc" prop="gio_ket_thuc">
          <el-time-picker
            v-model="form.gio_ket_thuc"
            placeholder="HH:mm"
            format="HH:mm"
            value-format="HH:mm"
            style="width: 100%"
          />
        </el-form-item>
        <el-form-item label="L do" prop="ly_do">
          <el-input
            v-model="form.ly_do"
            type="textarea"
            :rows="3"
            placeholder="M t cng vic tng ca"
          />
        </el-form-item>
        <el-alert
          title="Lu "
          type="info"
          :closable="false"
          description="H thng t ng kim tra gi tng ca theo gii hn: ti a 4h/ngy, 40h/thng, 300h/nm."
        />
      </el-form>
      <template #footer>
        <el-button @click="dialogVisible = false">Hy</el-button>
        <el-button type="primary" :loading="saving" @click="handleSubmit">
          Gi yu cu
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import {ref, reactive} from 'vue';
import {Plus} from '@element-plus/icons-vue';
import {ElMessage, FormInstance, FormRules} from 'element-plus';
import overtimeRequestService, {
  OvertimeRequest,
} from '@/services/overtimeRequestService';

const requests = ref<OvertimeRequest[]>([]);
const loading = ref(false);
const dialogVisible = ref(false);
const saving = ref(false);
const cancellingId = ref('');
const pagination = reactive({page: 1, limit: 10, total: 0});
const filters = reactive<{trang_thai: string}>({trang_thai: ''});

const formRef = ref<FormInstance>();
const form = reactive({
  ngay: '',
  gio_bat_dau: '',
  gio_ket_thuc: '',
  ly_do: '',
});

const rules: FormRules = {
  ngay: [{required: true, message: 'Vui lng chn ngy', trigger: 'change'}],
  gio_bat_dau: [{required: true, message: 'Nhp gi bt u'}],
  gio_ket_thuc: [{required: true, message: 'Nhp gi kt thc'}],
};

const statusLabel = (status: OvertimeRequest['trang_thai']) => {
  switch (status) {
    case 'Da duyet':
      return ' duyt';
    case 'Bi tu choi':
      return 'B t chi';
    case 'Da huy':
      return ' hy';
    default:
      return 'Ch duyt';
  }
};

const statusTag = (status: OvertimeRequest['trang_thai']) => {
  if (status === 'Da duyet') return 'success';
  if (status === 'Bi tu choi') return 'danger';
  if (status === 'Da huy') return 'info';
  return 'warning';
};

const typeLabel = (type?: string) => {
  if (!type) return 'Ngy thng';
  switch (type) {
    case 'holiday':
      return 'L/Tt';
    case 'holiday_night':
      return 'L/Tt m';
    case 'weekend':
      return 'Ngy ngh';
    case 'weekend_night':
      return 'Ngy ngh m';
    case 'weekday_night':
      return 'Ngy thng m';
    default:
      return 'Ngy thng';
  }
};

const formatDate = (value?: string) => {
  if (!value) return '---';
  return new Date(value).toLocaleDateString('vi-VN');
};

const formatTime = (value?: string) => {
  if (!value) return '--:--';
  const date = new Date(value);
  if (!Number.isNaN(date.getTime())) {
    return date.toLocaleTimeString('vi-VN', {hour: '2-digit', minute: '2-digit'});
  }
  return value;
};

const formatTimeRange = (row: OvertimeRequest) => {
  return `${formatTime(row.thoi_gian_bat_dau)} - ${formatTime(row.thoi_gian_ket_thuc)}`;
};

const loadRequests = async () => {
  loading.value = true;
  try {
    const response = await overtimeRequestService.getMy({
      page: pagination.page,
      limit: pagination.limit,
      trang_thai: filters.trang_thai || undefined,
    });
    requests.value = response.data;
    pagination.total = response.pagination.total;
  } catch (err: any) {
    ElMessage.error(
      err.response?.data?.msg || 'Khng th ti danh sch tng ca',
    );
  } finally {
    loading.value = false;
  }
};

const handlePageChange = (page: number) => {
  pagination.page = page;
  loadRequests();
};

const handleFilterChange = () => {
  pagination.page = 1;
  loadRequests();
};

const openDialog = () => {
  form.ngay = '';
  form.gio_bat_dau = '';
  form.gio_ket_thuc = '';
  form.ly_do = '';
  dialogVisible.value = true;
};

const handleSubmit = async () => {
  if (!formRef.value) return;
  try {
    await formRef.value.validate();
  } catch {
    return;
  }
  saving.value = true;
  try {
    await overtimeRequestService.create({
      ngay: form.ngay,
      gio_bat_dau: form.gio_bat_dau,
      gio_ket_thuc: form.gio_ket_thuc,
      ly_do: form.ly_do,
    });
    ElMessage.success(' gi yu cu tng ca');
    dialogVisible.value = false;
    loadRequests();
  } catch (err: any) {
    ElMessage.error(
      err.response?.data?.msg || 'Khng th gi yu cu',
    );
  } finally {
    saving.value = false;
  }
};

const handleCancel = async (id: string) => {
  try {
    cancellingId.value = id;
    await overtimeRequestService.cancel(id);
    ElMessage.success(' hy yu cu tng ca');
    loadRequests();
  } catch (err: any) {
    ElMessage.error(err.response?.data?.msg || 'Khng th hy yu cu');
  } finally {
    cancellingId.value = '';
  }
};

loadRequests();
</script>

<style scoped lang="scss">
.my-overtime {
  display: flex;
  flex-direction: column;
  gap: $spacing-md;
}

.header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: $spacing-sm;
}

.filters {
  display: flex;
  gap: $spacing-sm;
  flex-wrap: wrap;
}

.pagination {
  display: flex;
  justify-content: flex-end;
  padding-top: $spacing-md;
}
</style>
