const hasLoadedRequests = ref(false);

const loadEmployeeInfo = async () => {
  loading.value = true;
  try {
    // Get current logged-in user
    const user = authService.getUser();

    if (!user?.nhan_vien_id) {
      ElMessage.error('Khng tm thy thng tin nhn vin ca bn');
      return;
    }

    // Load employee information by employee ID
    employee.value = await nhanVienService.getById(user.nhan_vien_id);
  } catch (err: any) {
    console.error('Error loading employee info:', err);
    ElMessage.error(
      err.response?.data?.msg || 'Khng th ti thng tin c nhn',
    );
  } finally {
    loading.value = false;
  }
};

const handleTabClick = (tab: any) => {
  if (tab.paneName === 'payslips' && !hasLoadedPayslips.value) {
    loadPayslips();
  }
  if (tab.paneName === 'profileRequests' && !hasLoadedRequests.value) {
    loadProfileRequests();
  }
};

const loadPayslips = async () => {
  payslipLoading.value = true;
  try {
    const data = await payrollService.getMyPayslips();
    payslips.value = data;
    hasLoadedPayslips.value = true;
  } catch (err: any) {
    console.error('Error loading payslips:', err);
    ElMessage.error(err.response?.data?.msg || 'Khng th ti phiu lng');
  } finally {
    payslipLoading.value = false;
  }
};

const loadProfileRequests = async (force = false) => {
  if (requestLoading.value) return;
  if (!force && hasLoadedRequests.value) return;
  requestLoading.value = true;
  try {
    profileRequests.value = await profileRequestService.getMy();
    hasLoadedRequests.value = true;
  } catch (err: any) {
    console.error('Error loading profile requests:', err);
    ElMessage.error(
      err.response?.data?.msg || 'Khng th ti danh sch yu cu',
    );
  } finally {
    requestLoading.value = false;
  }
};

const requestTypeLabel = (type: ProfileRequest['type']) => {
  switch (type) {
    case 'personal':
      return 'Thng tin c nhn';
    case 'contact':
      return 'Lin h & a ch';
    case 'dependents':
      return 'Ngi ph thuc';
    default:
      return type;
  }
};

const requestStatusLabel = (status: ProfileRequest['status']) => {
  if (status === 'Pending') return 'Ch duyt';
  if (status === 'Approved') return ' duyt';
  if (status === 'Rejected') return 'T chi';
  return status;
};

const requestStatusTag = (status: ProfileRequest['status']) => {
  if (status === 'Approved') return 'success';
  if (status === 'Rejected') return 'danger';
  return 'warning';
};

const formatRequestDate = (value?: string) => {
  if (!value) return '---';
  return new Date(value).toLocaleString('vi-VN');
};

const handleProfileRequestSubmitted = () => {
  loadProfileRequests(true);
};

const handleAvatarUpload = async (file: File) => {
  // Validate file type
  const allowedTypes = ['image/jpeg', 'image/png', 'image/jpg'];
  if (!allowedTypes.includes(file.type)) {
    ElMessage.error('Ch chp nhn file nh JPG, PNG');
    return false;
  }

  // Validate file size (max 5MB)
  const maxSize = 5 * 1024 * 1024;
  if (file.size > maxSize) {
    ElMessage.error('Kch thc file khng c vt qu 5MB');
    return false;
  }

  try {
    const user = authService.getUser();
    if (!user?.nhan_vien_id) {
      ElMessage.error('Khng tm thy thng tin nhn vin');
      return false;
    }

    await uploadService.uploadEmployeePhoto(user.nhan_vien_id, file);
    ElMessage.success('Ti nh i din thnh cng');
    await loadEmployeeInfo();
  } catch (err: any) {
    console.error('Error uploading avatar:', err);
    ElMessage.error(err.response?.data?.msg || 'Khng th ti nh i din');
  }

  return false;
};

const getEmploymentStatusType = (status: string): string => {
  const statusMap: Record<string, string> = {
    'ang lm vic': 'success',
    'Ton thi gian': 'success',
    'Bn thi gian': 'primary',
    'Th vic': 'warning',
    'Ngh vic': 'info',
    'Tm ngh': 'danger',
    'Thc tp': 'primary',
  };
  return statusMap[status] || 'info';
};

const formatCurrency = (value?: number, currency = 'VND') => {
  try {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: currency || 'VND',
      maximumFractionDigits: 0,
    }).format(value || 0);
  } catch {
    return `${(value || 0).toLocaleString('vi-VN')} ${currency}`;
  }
};

const formatDateRange = (start?: string, end?: string) => {
  if (!start || !end) return '---';
  return `${new Date(start).toLocaleDateString('vi-VN')} - ${new Date(
    end,
  ).toLocaleDateString('vi-VN')}`;
};

const runStatusLabel = (status: PayrollRunStatus) => {
  switch (status) {
    case 'Draft':
      return 'Nhp';
    case 'Cho_duyet':
      return 'Ch duyt';
    case 'Da_duyet':
      return ' duyt';
    case 'Da_chi':
      return ' chi';
    default:
      return status;
  }
};

const runStatusTag = (status: PayrollRunStatus) => {
  if (status === 'Da_duyet' || status === 'Da_chi') return 'success';
  if (status === 'Cho_duyet') return 'warning';
  return 'info';
};

const entryStatusLabel = (status: 'Cho_duyet' | 'Da_duyet' | 'Da_chi') => {
  switch (status) {
    case 'Da_duyet':
      return 'Phiu duyt';
    case 'Da_chi':
      return ' chi tr';
    default:
      return 'Ch duyt';
  }
};

onMounted(() => {
  loadEmployeeInfo();
});

